    color: var(--st-text-muted);
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-align: center;
}

/*  */
.heatmap-grid {
    display: grid;
    grid-template-columns: repeat(53, minmax(0, 1fr));
    gap: 4px;
    min-width: 700px;
}

.heatmap-cell {
    aspect-ratio: 1;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.2s ease;
    background-color: rgba(255, 255, 255, 0.03); 
}

.heatmap-cell:hover {
    transform: scale(1.3);
    box-shadow: 0 0 12px var(--st-primary-glow);
    z-index: 10;
}

.heatmap-cell.active {
    border: 2px solid #fff !important;
    transform: scale(1.2);
}

.heatmap-level-0 { background-color: rgba(255, 255, 255, 0.03); }
.heatmap-level-1 { background-color: var(--st-heatmap-level-1); }
.heatmap-level-2 { background-color: var(--st-heatmap-level-2); }
.heatmap-level-3 { background-color: var(--st-heatmap-level-3); }
.heatmap-level-4 { background-color: var(--st-heatmap-level-4); }

.heatmap-scroll-wrapper {
    overflow-x: auto;
    padding-bottom: 10px;
    scrollbar-color: var(--st-card-border) transparent;
    scrollbar-width: thin;
}

.heatmap-scroll-wrapper::-webkit-scrollbar {
    height: 6px;
}

.heatmap-scroll-wrapper::-webkit-scrollbar-thumb {
    background-color: var(--st-card-border);
    border-radius: 3px;
}

#heatmap-details-panel {
