/**
 * SillyTavern Chat Statistics Extension
 * Main entry point
 */
import './styles.css';
import { Logger } from './logger.js';
import { CONFIG } from './config.js';
import { fetchAllChats, fetchAllCharactersChats } from './api.js';
import { analyzeChats, parseDate, formatLocalDateKey } from './analyzer.js';
import { showOverlay, generateDashboardHTML, setupDashboardEvents, closeOverlay, initCharts, THEMES } from './ui.js';

const logger = new Logger('Stats');

const EXTENSION_NAME = CONFIG.EXTENSION_NAME;
let settings = null;

function getContextSafe() {
    if (!globalThis.SillyTavern?.getContext) return null;
    return globalThis.SillyTavern.getContext();
}

function escapeHtml(value) {
    return String(value ?? '')
        .replace(/&/g, '&amp;')
        .replace(/</g, '&lt;')
        .replace(/>/g, '&gt;')
        .replace(/"/g, '&quot;')
        .replace(/'/g, '&#39;');
}

function ensureSettings() {
    if (settings) return settings;
    const context = getContextSafe();
    if (!context) {
        throw new Error('SillyTavern context not available');
    }

    const { extensionSettings } = context;
    if (!extensionSettings[EXTENSION_NAME]) {
        logger.log('Initializing default settings for the first time.');
        extensionSettings[EXTENSION_NAME] = structuredClone(CONFIG.DEFAULT_SETTINGS);
    }

    settings = extensionSettings[EXTENSION_NAME];

    // Ensure default theme is set (for existing users)
    if (!settings.theme) {
        settings.theme = CONFIG.DEFAULT_SETTINGS.theme || 'violet';
    }

    // Ensure cache cleanup settings are set (for existing users)
    if (settings.cacheCleanupEnabled === undefined) {
        settings.cacheCleanupEnabled = CONFIG.DEFAULT_SETTINGS.cacheCleanupEnabled;
    }
    if (!Number.isFinite(settings.cacheCleanupDays)) {
        settings.cacheCleanupDays = CONFIG.DEFAULT_SETTINGS.cacheCleanupDays;
    }

    return settings;
}

const CACHE_BACKUP_KEY = 'ST_Stats_CacheBackup';

// Save settings helper (prefer immediate save)
function saveSettingsNow() {
    try {
        const ctx = getContextSafe();
        if (ctx && typeof ctx.saveSettings === 'function') {
            ctx.saveSettings();
            logger.log('Settings saved immediately via context.saveSettings');
        } else if (ctx && typeof ctx.saveSettingsDebounced === 'function') {
            ctx.saveSettingsDebounced();
            logger.log('Settings saved via context.saveSettingsDebounced');
        } else if (typeof globalThis.saveSettings === 'function') {
            globalThis.saveSettings();
            logger.log('Settings saved via globalThis.saveSettings');
        } else if (typeof globalThis.saveSettingsDebounced === 'function') {
            globalThis.saveSettingsDebounced();
            logger.log('Settings saved via globalThis.saveSettingsDebounced');
        } else {
            logger.warn('No save function available!');
        }
    } catch (e) {
        logger.error('Failed to save settings:', e);
    }
}

// Abort controller for cancelling operations
let currentAbortController = null;

/**
 * Generate and display statistics report
 * @param {boolean} forceRefresh - Force refresh data
 * @param {boolean} globalMode - Generate global stats for all characters
 */
async function generateReport(forceRefresh = false, globalMode = false, dateRange = null) {
    try {
        ensureSettings();
    } catch (error) {
        logger.error('Settings unavailable:', error);
        toastr.error('', 'Stats');
        return;
    }
    // Cancel any existing operation
    if (currentAbortController) {
        currentAbortController.abort();
    }
    currentAbortController = new AbortController();
    const abortSignal = currentAbortController.signal;

    const context = getContextSafe();
    if (!context) {
        toastr.error('', 'Stats');
        return;
    }
    const characterId = context.characterId;

    // Determine mode: if no character selected, use global mode
    const isGlobalMode = globalMode || characterId == null;

    let character = null;
    let avatarUrl = null;
    let cacheKey = null;
    let baseCacheKey = null;

    if (!isGlobalMode) {
        character = context.characters[characterId];
        if (!character) {
            toastr.error('', 'Stats');
            return;
        }
        avatarUrl = character.avatar;
        cacheKey = characterId;
    } else {
        cacheKey = '__global__';
    }

    baseCacheKey = cacheKey;

    const rangeKey = dateRange && (dateRange.start || dateRange.end)
        ? `__${dateRange.start || ''}_${dateRange.end || ''}`
        : '';
    cacheKey = `${baseCacheKey}${rangeKey}`;

    const reportTitle = isGlobalMode ? '' : character.name;
    const reportTitleSafe = escapeHtml(reportTitle);

    // Save to localStorage as backup
    const saveToLocalStorage = (cacheData) => {
        try {
            const backup = JSON.parse(localStorage.getItem(CACHE_BACKUP_KEY) || '{}');
            backup[cacheKey] = cacheData;
            backup.__index = backup.__index || {};
            backup.__index[baseCacheKey] = cacheKey;
            localStorage.setItem(CACHE_BACKUP_KEY, JSON.stringify(backup));
            logger.log('Cache backed up to localStorage');
        } catch (e) {
            logger.error('localStorage backup failed:', e);
        }
    };

    // Load from localStorage backup
    const loadFromLocalStorage = () => {
        try {
            const backup = JSON.parse(localStorage.getItem(CACHE_BACKUP_KEY) || '{}');
            return backup;
        } catch (e) {
            return {};
        }
    };

    

    // Helper to setup events with correct context
    const bindEvents = (statsToUse) => {
        setupDashboardEvents((force, range, newTheme) => {
            if (newTheme) {
                // Update theme setting
                settings.theme = newTheme;
                if (globalThis.SillyTavern.saveSettingsDebounced) {
                    globalThis.SillyTavern.saveSettingsDebounced();
                }
                
                // Re-render dashboard with new theme (no data fetch needed)
                const html = generateDashboardHTML(statsToUse, isGlobalMode ? null : character, isGlobalMode, settings.theme);
                $('#stats-content-wrapper').html(html);
                
                // Update overlay theme
                const themeClass = (THEMES[newTheme] || THEMES.violet).class;
                $('#stats-overlay').removeClass().addClass(themeClass);

                initCharts(statsToUse, settings.theme);
                // Re-bind events
                bindEvents(statsToUse);
            } else {
                // Refresh data or date range
                generateReport(force, isGlobalMode, range);
            }
        });
    };

    // Check cache
    let cachedEntry = (!forceRefresh && settings.cache) ? settings.cache[cacheKey] : null;
    logger.log(`Cache check: forceRefresh=${forceRefresh}, cacheKey=${cacheKey}, hasCacheEntry=${!!cachedEntry}, cacheKeys=${settings.cache ? Object.keys(settings.cache).join(',') : 'none'}`);
    
    if (!forceRefresh && settings.cache && !cachedEntry && settings.cacheIndex && settings.cacheIndex[baseCacheKey]) {
        const indexedKey = settings.cacheIndex[baseCacheKey];
        logger.log(`Trying indexed key: ${indexedKey}`);
        if (settings.cache[indexedKey]) {
            cachedEntry = settings.cache[indexedKey];
            cacheKey = indexedKey;
        }
    }
    if (!forceRefresh && settings.cache && !cachedEntry) {
        const candidateKeys = Object.keys(settings.cache).filter(key => key === baseCacheKey || key.startsWith(`${baseCacheKey}__`));
        logger.log(`Trying candidate keys: ${candidateKeys.join(',')}`);
        if (candidateKeys.length > 0) {
            candidateKeys.sort((a, b) => (settings.cache[b].updatedAt || 0) - (settings.cache[a].updatedAt || 0));
            cachedEntry = settings.cache[candidateKeys[0]];
            cacheKey = candidateKeys[0];
        }
    }

    // Try localStorage backup if no valid cache found or dailyDuration is empty
    if (!forceRefresh && cachedEntry) {
        const hasDailyDuration = cachedEntry.stats?.dailyDuration && Object.keys(cachedEntry.stats.dailyDuration).length > 0;
        if (!hasDailyDuration) {
            logger.log('Cache has empty dailyDuration, trying localStorage backup...');
            const backup = loadFromLocalStorage();
            const backupKey = backup.__index?.[baseCacheKey] || cacheKey;
            if (backup[backupKey] && backup[backupKey].stats?.dailyDuration && Object.keys(backup[backupKey].stats.dailyDuration).length > 0) {
                logger.log(`Found valid backup in localStorage for ${backupKey}`);
                cachedEntry = backup[backupKey];
                cacheKey = backupKey;
                // Restore to settings cache
                settings.cache[cacheKey] = cachedEntry;
            }
        }
    }

    // If still no cache, try localStorage directly
    if (!forceRefresh && !cachedEntry) {
        logger.log('No cache in settings, trying localStorage backup...');
        const backup = loadFromLocalStorage();
        const backupKey = backup.__index?.[baseCacheKey];
        if (backupKey && backup[backupKey]) {
            logger.log(`Found backup in localStorage for ${backupKey}`);
            cachedEntry = backup[backupKey];
            cacheKey = backupKey;
            // Restore to settings cache
            if (!settings.cache) settings.cache = {};
            settings.cache[cacheKey] = cachedEntry;
        }
    }

    if (!forceRefresh && cachedEntry) {
        logger.log(`Using cached stats for ${reportTitle}, has dailyDuration: ${!!cachedEntry.stats?.dailyDuration}, keys: ${Object.keys(cachedEntry.stats?.dailyDuration || {}).length}`);
        const cachedStats = cachedEntry.stats ? cachedEntry.stats : cachedEntry;
        if (!cachedStats.__meta) {
            const dateRangeFromCache = cachedEntry.dateRange || dateRange || null;
            const dateBoundsFromCache = cachedEntry.dateBounds || null;
            cachedStats.__meta = { dateRange: dateRangeFromCache, dateBounds: dateBoundsFromCache };
        }
        // Ensure new fields exist for old cached data
        if (!cachedStats.dailyDuration) {
            cachedStats.dailyDuration = {};
        }
        if (cachedStats.overview && cachedStats.overview.totalDurationMinutes === undefined) {
            cachedStats.overview.totalDurationMinutes = 0;
        }
        const dashboardHTML = generateDashboardHTML(cachedStats, isGlobalMode ? null : character, isGlobalMode, settings.theme);
        showOverlay(dashboardHTML, settings.theme);
        // Initialize charts for cached data
        initCharts(cachedStats, settings.theme);
        bindEvents(cachedStats);
        toastr.success('', 'Stats');
        return;
    }

    // Clear cache if force refresh
    if (forceRefresh && settings.cache && settings.cache[cacheKey]) {
        logger.log(`Clearing cache for ${reportTitle}`);
        delete settings.cache[cacheKey];
        saveSettingsNow();
    }

    // Show loading overlay
    showOverlay(`
        <div id="stats-container" class="stats-dashboard" style="min-height: 300px; justify-content: center; align-items: center;">
            <div class="stats-actions">
                <div class="stats-btn refresh-btn" title=" (Refresh)">
                    <i class="fa-solid fa-arrows-rotate"></i>
                </div>
                <div class="stats-btn download-btn" title="Save as Image">
                    <i class="fa-solid fa-download"></i>
                </div>
                <div class="stats-btn close-btn" title="Close">
                    <i class="fa-solid fa-xmark"></i>
                </div>
            </div>
            <h3><i class="fa-solid fa-chart-simple"></i> : ${reportTitleSafe}</h3>
            <div id="stats-progress-area" style="text-align: center; width: 100%; max-width: 600px;">
                <p id="stats-status-text" style="margin-bottom: 10px;">...</p>
                <progress id="stats-progress-bar" value="0" max="100" style="width: 100%; height: 20px;"></progress>
                <p id="stats-count-text" style="font-size: 0.9em; color: #888; margin-top: 5px;">0 / 0</p>
            </div>
        </div>
    `, settings.theme);

    // Setup cancel button for loading screen
    $('#stats-overlay').off('click', '.close-btn').on('click', '.close-btn', () => {
        if (currentAbortController) {
            currentAbortController.abort();
            currentAbortController = null;
        }
        closeOverlay();
    });

    try {
        let chatsData;

        if (isGlobalMode) {
            // Progress callback for global mode
            const onProgress = (currentChar, totalChars, totalChats) => {
                const percentage = Math.round((currentChar / totalChars) * 100);
                $('#stats-progress-bar').val(percentage);
                $('#stats-count-text').text(` ${currentChar} / ${totalChars} ${totalChats} `);
                $('#stats-status-text').text(`: ${percentage}%`);
            };

            chatsData = await fetchAllCharactersChats(onProgress, abortSignal);
        } else {
            // Progress callback for single character
            const onProgress = (current, total) => {
                if (total > 5) {
                    const percentage = Math.round((current / total) * 100);
                    $('#stats-progress-bar').val(percentage);
                    $('#stats-count-text').text(`${current} / ${total}`);
                    $('#stats-status-text').text(`: ${percentage}%`);
                } else {
                    $('#stats-status-text').text('...');
                }
            };

            chatsData = await fetchAllChats(avatarUrl, onProgress, abortSignal);
        }

        if (chatsData.length === 0) {
            const emptyMessage = isGlobalMode ? '' : '';
            $('#stats-content-wrapper').html(`
                <div class="stats-dashboard" style="justify-content: center; align-items: center; min-height: 200px;">
                    <div class="stats-actions">
                        <div class="stats-btn refresh-btn" title=" (Refresh)">
                            <i class="fa-solid fa-arrows-rotate"></i>
                        </div>
                        <div class="stats-btn download-btn" title="Save as Image">
                            <i class="fa-solid fa-download"></i>
                        </div>
                        <div class="stats-btn close-btn" title="Close">
                            <i class="fa-solid fa-xmark"></i>
                        </div>
                    </div>
                    <div style="text-align: center;">
                        <i class="fa-solid fa-circle-exclamation fa-3x" style="color: #ffcc00; margin-bottom: 15px;"></i>
                        <p>${emptyMessage}</p>
                    </div>
                </div>
            `);
            setupDashboardEvents((force, range) => {
                generateReport(force, isGlobalMode, range);
            });
            return;
        }

        // Compute date bounds from all chats (for range picker)
        let minDate = null;
        let maxDate = null;
        chatsData.forEach(chat => {
            chat.messages.forEach(msg => {
                const date = parseDate(msg.send_date);
                if (!date) return;
                if (!minDate || date < minDate) minDate = date;
                if (!maxDate || date > maxDate) maxDate = date;
            });
        });

        const dateBounds = {
            min: minDate ? formatLocalDateKey(minDate) : '',
            max: maxDate ? formatLocalDateKey(maxDate) : ''
        };

        const normalizedRange = {
            start: dateRange?.start || dateBounds.min || '',
            end: dateRange?.end || dateBounds.max || ''
        };

        // Analyze data
        $('#stats-status-text').text('...');
        await new Promise(resolve => setTimeout(resolve, 50));

        const stats = analyzeChats(chatsData, {
            startDate: normalizedRange.start,
            endDate: normalizedRange.end
        });
        stats.__meta = { dateRange: normalizedRange, dateBounds };

        // Cache results
        if (!settings.cache) {
            settings.cache = {};
        }
        const cacheData = { stats, dateRange: normalizedRange, dateBounds, updatedAt: Date.now() };
        settings.cache[cacheKey] = cacheData;
        if (!settings.cacheIndex) {
            settings.cacheIndex = {};
        }
        settings.cacheIndex[baseCacheKey] = cacheKey;
        
        // Log cache data for debugging
        logger.log(`Caching stats for ${cacheKey}, totalDuration: ${stats.overview?.totalDurationMinutes}m, dailyDuration keys: ${Object.keys(stats.dailyDuration || {}).length}`);
        
        // Save to localStorage as reliable backup
        saveToLocalStorage(cacheData);
        
        // Save settings
        saveSettingsNow();

        // Display dashboard
        const dashboardHTML = generateDashboardHTML(stats, isGlobalMode ? null : character, isGlobalMode, settings.theme);
        $('#stats-content-wrapper').html(dashboardHTML);
        // Initialize charts for fresh data
        initCharts(stats, settings.theme);
        bindEvents(stats);

        toastr.success('', 'Stats');

    } catch (error) {
        logger.error('Report generation failed:', error);
        const errorMessage = escapeHtml(error?.message || 'Unknown error');
        $('#stats-content-wrapper').html(`
            <div class="stats-dashboard" style="justify-content: center; align-items: center; min-height: 200px;">
                <div class="stats-actions">
                    <div class="stats-btn refresh-btn" title=" (Refresh)">
                        <i class="fa-solid fa-arrows-rotate"></i>
                    </div>
                    <div class="stats-btn download-btn" title="Save as Image">
                        <i class="fa-solid fa-download"></i>
                    </div>
                    <div class="stats-btn close-btn" title="Close">
                        <i class="fa-solid fa-xmark"></i>
                    </div>
                </div>
                <div style="text-align: center;">
                    <i class="fa-solid fa-triangle-exclamation fa-3x" style="color: #ff0000; margin-bottom: 15px;"></i>
                    <p style="color:red;">: ${errorMessage}</p>
                </div>
            </div>
        `);
        setupDashboardEvents((force, range) => {
            generateReport(force, isGlobalMode, range);
        });
    }
}

/**
 * Add button to wand menu
 */
function addWandMenuButton() {
    const buttonId = CONFIG.WAND_BUTTON_ID;
    
    if ($(`#${buttonId}`).length > 0) {
        return; // Already exists
    }

    const buttonHTML = `
        <div id="${buttonId}" class="list-group-item flex-container flexGap5 interactable">
            <div class="fa-solid fa-chart-pie"></div>
            <span></span>
        </div>
    `;

    const extensionsMenu = $('#extensionsMenu');
    if (extensionsMenu.length > 0) {
        extensionsMenu.append(buttonHTML);
        $(`#${buttonId}`).on('click', () => {
            generateReport(false);
        });
        logger.log('Added Wand Menu button.');
    } else {
        logger.warn('Wand menu container (#extensionsMenu) not found.');
    }
}

function cleanupCache(days) {
    ensureSettings();
    if (!settings.cache) return 0;
    const cutoff = Date.now() - days * 24 * 60 * 60 * 1000;
    let removed = 0;

    Object.keys(settings.cache).forEach((key) => {
        const entry = settings.cache[key];
        const updatedAt = entry?.updatedAt || 0;
        if (updatedAt && updatedAt < cutoff) {
            delete settings.cache[key];
            removed++;
        }
    });

    if (settings.cacheIndex) {
        Object.keys(settings.cacheIndex).forEach((baseKey) => {
            const key = settings.cacheIndex[baseKey];
            if (!settings.cache[key]) {
                delete settings.cacheIndex[baseKey];
            }
        });
    }

    return removed;
}

function cleanupLocalStorageBackup(days) {
    ensureSettings();
    const cutoff = Date.now() - days * 24 * 60 * 60 * 1000;
    let removed = 0;

    try {
        const backup = JSON.parse(localStorage.getItem(CACHE_BACKUP_KEY) || '{}');
        Object.keys(backup).forEach((key) => {
            if (key === '__index') return;
            const entry = backup[key];
            const updatedAt = entry?.updatedAt || 0;
            if (updatedAt && updatedAt < cutoff) {
                delete backup[key];
                removed++;
            }
        });

        if (backup.__index) {
            Object.keys(backup.__index).forEach((baseKey) => {
                const key = backup.__index[baseKey];
                if (!backup[key]) {
                    delete backup.__index[baseKey];
                }
            });
        }

        localStorage.setItem(CACHE_BACKUP_KEY, JSON.stringify(backup));
    } catch (e) {
        logger.error('localStorage cleanup failed:', e);
    }

    return removed;
}

function runCacheCleanup(days, reason = 'manual') {
    ensureSettings();
    const safeDays = Math.max(1, Math.floor(Number(days) || CONFIG.DEFAULT_SETTINGS.cacheCleanupDays));
    const removedCache = cleanupCache(safeDays);
    const removedBackup = cleanupLocalStorageBackup(safeDays);

    if (removedCache > 0 || removedBackup > 0) {
        saveSettingsNow();
    }

    logger.log(`Cache cleanup (${reason}): settings=${removedCache}, localStorage=${removedBackup}, days=${safeDays}`);
    return { removedCache, removedBackup };
}

/**
 * Add settings panel
 */
function addSettingsPanel() {
    ensureSettings();
    const panelHTML = `
        <div class="stats-settings">
            <div class="inline-drawer">
                <div class="inline-drawer-toggle inline-drawer-header">
                    <b></b>
                    <div class="inline-drawer-icon fa-solid fa-chart-bar down"></div>
                </div>
                <div class="inline-drawer-content stats-drawer">
                    <p></p>
                    <button id="${CONFIG.ANALYZE_BUTTON_ID}" class="menu_button">
                        <i class="fa-solid fa-calculator"></i> 
                    </button>
                    <button id="${CONFIG.ANALYZE_BUTTON_ID}_global" class="menu_button" style="margin-top: 5px;">
                        <i class="fa-solid fa-globe"></i> 
                    </button>
                    <div class="stats-setting-divider"></div>
                    <div class="stats-setting-row">
                        <label class="stats-setting-label">
                            <input type="checkbox" id="stats_cache_cleanup_enabled" /> 
                        </label>
                    </div>
                    <div class="stats-setting-row">
                        <label class="stats-setting-label"></label>
                        <input type="number" id="stats_cache_cleanup_days" class="stats-input" min="1" max="365" />
                        <button id="stats_cache_cleanup_now" class="menu_button stats-small-button">
                            <i class="fa-solid fa-broom"></i> 
                        </button>
                    </div>
                </div>
            </div>
        </div>
    `;

    $('#extensions_settings').append(panelHTML);
    $(`#${CONFIG.ANALYZE_BUTTON_ID}`).on('click', () => generateReport(false, false));
    $(`#${CONFIG.ANALYZE_BUTTON_ID}_global`).on('click', () => generateReport(false, true));

    const normalizeDays = (value) => {
        const num = Math.max(1, Math.floor(Number(value) || CONFIG.DEFAULT_SETTINGS.cacheCleanupDays));
        return Math.min(num, 365);
    };

    $('#stats_cache_cleanup_enabled').prop('checked', !!settings.cacheCleanupEnabled);
    $('#stats_cache_cleanup_days').val(settings.cacheCleanupDays);

    $('#stats_cache_cleanup_enabled').on('change', (e) => {
        settings.cacheCleanupEnabled = !!e.target.checked;
        saveSettingsNow();
        if (settings.cacheCleanupEnabled) {
            runCacheCleanup(settings.cacheCleanupDays, 'toggle-on');
        }
    });

    $('#stats_cache_cleanup_days').on('change', (e) => {
        const days = normalizeDays(e.target.value);
        settings.cacheCleanupDays = days;
        $('#stats_cache_cleanup_days').val(days);
        saveSettingsNow();
        if (settings.cacheCleanupEnabled) {
            runCacheCleanup(days, 'days-change');
        }
    });

    $('#stats_cache_cleanup_now').on('click', () => {
        const days = normalizeDays($('#stats_cache_cleanup_days').val());
        const result = runCacheCleanup(days, 'manual');
        if (globalThis.toastr) {
            globalThis.toastr.success(` ${result.removedCache}  ${result.removedBackup} `, 'Stats');
        }
    });
}

/**
 * Initialize extension
 */
jQuery(async () => {
    try {
        // Wait for SillyTavern to be ready
        await new Promise((resolve) => {
            const checkInterval = setInterval(() => {
                if (globalThis.SillyTavern?.getContext) {
                    clearInterval(checkInterval);
                    resolve();
                }
            }, 100);
        });

        ensureSettings();

        // Add UI elements
        addSettingsPanel();
        addWandMenuButton();

        // Auto cleanup cache on startup if enabled
        if (settings.cacheCleanupEnabled) {
            runCacheCleanup(settings.cacheCleanupDays, 'startup');
        }

        logger.log('Extension initialized.');
    } catch (error) {
        logger.error('Initialization failed:', error);
    }
});
